export default {
  data () {
    return {
      offset: 100,
      link: ''
    }
  },
  methods: {
    closePlayer () {
      this.isShow(false)

      this.link = ''
    },
    setVideoData (option) {
      this.isShow(true)

      this.link = option.url
    },
    isShow (show) {
      this.offset = show ? 0 : 100
    }
  }
}



